@import url(https://fonts.googleapis.com/css2?family=Sora:wght@200;400;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{width:100%;height:100vh;display:-webkit-flex;display:flex;place-content:center;-webkit-align-items:center;align-items:center;font-family:"Sora","Helvetica","Source Sans Pro",sans-serif;background-color:#f3b8c6;color:#000}#quote-box{margin:2rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;max-width:500px;background-color:rgba(255,255,255,.85);border-radius:1rem;box-shadow:0 1px 3px 1px rgba(231,133,154,.3)}#text-container{width:100%}#text-container #text{width:100%;height:100%;padding:1rem;font-size:25px;line-height:35px;text-align:left;background-color:#fff;border-radius:1rem 1rem 0 0}#text-container::before,#text-container::after{position:relative;z-index:1;font-size:100px;line-height:35px;color:#e7859a}#text-container::before{right:.8rem;margin-right:-2rem;float:left;content:"“"}#text-container::after{bottom:-1rem;left:1rem;margin-left:-2rem;float:right;content:"”"}#author{margin-left:1rem;font-weight:bold}#author::before{content:"— ";font-weight:400}#buttons-container{width:100%;margin:2rem 0 1rem;display:-webkit-flex;display:flex;place-content:center}#buttons-container #tweet-quote,#buttons-container #new-quote{font:inherit;font-size:1rem;text-decoration:none;margin-left:1rem;padding:.5rem;border:none;border-radius:10px;background-color:#1da1f2;color:#fff}.footer{color:#fff;text-align:center}.footer a{color:#e7859a}
